import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function SimplePresent() {
  return (
    <>
      <div className="rule-box">
        <strong>Simple Present</strong> ={" "}
        <Key>alkanlklar, genel dorular</Key>. <Term>He/She/It</Term> iin{" "}
        <Key>fiil + -s/-es/-ies</Key>.
      </div>

      <Sub title="Kurulu (Formation)" />
      <Formula
        rows={[
          ["Olumlu", "zne + Fiil1 (+s/es/ies)", "He plays. / They play."],
          ["Olumsuz", "zne + do/does + not + Fiil1", "She doesnt like tea."],
          ["Soru", "Do/Does + zne + Fiil1 ?", "Does he live in Izmir?"],
        ]}
      />

      <Sub title="He/She/It: Yazm (Spelling)" />
      <MiniTable
        head={["Kural", "rnek", "Not"]}
        rows={[
          ["-s", "playplays", "ou fiil"],
          ["-es", "watchwatches, gogoes", "-ch/-sh/-o/-x/-ss"],
          ["-ies", "studystudies", "nsz + y  -ies"],
          ["havehas", "havehas", "zel durum"],
        ]}
      />

      <Sub title="Sklk Zarflar (Adverbs of Frequency  position)" />
      <MiniTable
        head={["Zarf", "Kullanm", "rnek"]}
        rows={[
          [
            "always / usually / often / sometimes / never",
            "zne + zarf + fiil",
            "I often read at night.",
          ],
          ["be + zarf", "zne + be + zarf", "She is always late."],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog (Examples & Mini-Dialogue)" />
      <Examples
        items={[
          {
            en: "I work every day.",
            tr: "Her gn alrm.",
            pron: "ay vrk evri dey",
          },
          {
            en: "He plays football.",
            tr: "O futbol oynar.",
            pron: "hii pleyz futbol",
          },
          {
            en: "She doesnt like tea.",
            tr: "O ay sevmez.",
            pron: "ii daznt layk tii",
          },
          {
            en: "Do you live in Ankara?",
            tr: "Ankarada m yayorsun?",
            pron: "du yu lv n ankra",
          },
        ]}
      />
      <Callout type="tip" title="Mini-Diyalog (Mini-Dialogue)">
        <p>
          <strong>A:</strong> Do you usually get up early?
          <br />
          <strong>B:</strong> Yes, I do. I always get up at 6.
        </p>
      </Callout>

      <Callout type="error" title="Yaygn Hatalar (Common Mistakes)">
        <ul className="compact-list">
          <li>
            <em>He dont like</em>   <Term>He doesnt like</Term> 
          </li>
          <li>
            <em>She doesnt likes</em>   <Term>She doesnt like</Term> 
          </li>
          <li>
            <em>He have</em>   <Term>He has</Term> 
          </li>
        </ul>
      </Callout>

      <Sub title="Mini Szlk (Daily Routine)" />
      <MiniTable
        head={["Fiil", "TR", "rnek"]}
        rows={[
          ["get up", "kalkmak", "I get up at 7."],
          ["have breakfast", "kahvalt etmek", "She has breakfast at 8."],
          [
            "go to work/school",
            "ie/okula gitmek",
            "They go to school by bus.",
          ],
          [
            "read / watch / study",
            "okumak/izlemek/almak",
            "He reads in the evening.",
          ],
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Fiili doru biimde kullan (Use the correct verb form)"
        items={[
          {
            id: "s1",
            before: "He ____ (play) tennis on Mondays.",
            after: "",
            answers: ["plays"],
          },
          {
            id: "s2",
            before: "They ____ (not / like) coffee.",
            after: "",
            answers: ["don't like", "do not like"],
          },
          {
            id: "s3",
            before: "____ she ____ (live) in Berlin?",
            after: "",
            answers: ["does she live"],
          },
          {
            id: "s4",
            before: "I ____ (often / read) books at night.",
            after: "",
            answers: ["often read"],
          },
          {
            id: "s5",
            before: "She ____ (have) two brothers.",
            after: "",
            answers: ["has"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangisi dorudur?",
            choices: [
              "She dont likes pizza.",
              "She doesnt like pizza.",
              "She doesnt likes pizza.",
            ],
            correctIndex: 1,
            explain: "doesnt + fiil1 (sz).",
          },
          {
            q: "Hangi cmle Simple Present?",
            choices: [
              "I am reading now.",
              "I read every day.",
              "I am going to read.",
            ],
            correctIndex: 1,
            explain: "Rutin/alkanlk: I read every day.",
          },
        ]}
      />
    </>
  );
}
