import React from "react";
import {
  Term,
  MiniTable,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  Key,
  Sub,
} from "../../../blocks";

export default function SubjectPronouns() {
  return (
    <>
      <div className="rule-box">
        zne zamirleri: <Term>I, you, he, she, it, we, you, they</Term>.
        Trkedeki <em>o</em> ngilizcede <Term>he/she/it</Term> olarak ayrlr.
        <Term>you</Term> tekil/oul olabilir. <Key>it</Key> insan iin
        kullanlmaz.
      </div>

      <Sub title="Tablo & Notlar (Table & Notes)" />
      <MiniTable
        head={["EN", "TR telaffuz", "Trke", "Not"]}
        rows={[
          ["I", "ay", "ben", "Kendinden bahsederken"],
          ["you", "yu", "sen/siz", "Tekil & oul"],
          ["he", "hii", "o (erkek)", "Erkek kii"],
          ["she", "ii", "o (kadn)", "Kadn kii"],
          ["it", "t", "o (nesne/hayvan)", "Hava/saat iin de"],
          ["we", "vii", "biz", "Konuan + bakas"],
          ["they", "dey", "onlar", "nsan/nesne oul"],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog (Examples & Mini-Dialogue)" />
      <Examples
        items={[
          {
            en: "She is my sister.",
            tr: "O (kadn) kz kardeim.",
            pron: "ii iz may sistr",
          },
          { en: "They are here.", tr: "Onlar burada.", pron: "dey ar hir" },
          { en: "It is cold.", tr: "Hava souk.", pron: "t iz kould" },
        ]}
      />
      <Callout type="tip" title="Mini-Diyalog (Mini-Dialogue)">
        <p>
          <strong>A:</strong> Who is Ali?
          <br />
          <strong>B:</strong> He is my friend.
        </p>
        <p className="hint">TR: Ali kim?  O (erkek) arkadam.</p>
      </Callout>

      <Callout type="error" title="it  he/she (insanlar iin kullanlmaz)">
        <Term>it</Term> insanlar iin kullanlmaz; hayvan/nesne/hava/saat iin
        kullanlr: <em>Its 5 oclock.</em>
      </Callout>

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        items={[
          {
            id: "sp1",
            before: "_____ am from Izmir.",
            after: "",
            answers: ["I", "i"],
          },
          {
            id: "sp2",
            before: "_____ is a doctor. (erkek)",
            after: "",
            answers: ["He", "he"],
          },
          {
            id: "sp3",
            before: "_____ are students.",
            after: "",
            answers: ["They", "they"],
          },
          {
            id: "sp4",
            before: "This is a cat. _____ is cute.",
            after: "",
            answers: ["It", "it"],
          },
          {
            id: "sp5",
            before: "_____ are my friends. (sen/siz)",
            after: "",
            answers: ["You", "you"],
          },
        ]}
        title="Boluk Doldurma (Gap-fill): Doru zneyi se"
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangi cmlede *it* dorudur?",
            choices: [
              "It are my mother.",
              "It is raining.",
              "It are students.",
            ],
            correctIndex: 1,
            explain: "Hava/yamur gibi durumlar iin it: It is raining.",
          },
          {
            q: "Hangisi oul zne deildir?",
            choices: ["they", "we", "he"],
            correctIndex: 2,
            explain: "he tekildir.",
          },
        ]}
      />
    </>
  );
}
