// src/pages/Grammar/GrammarGerA2.jsx
import React, { useMemo, useState, useEffect, useRef } from "react";
import "../Grammar.scss";
import { Link } from "react-router-dom";
import SectionNav from "../../../components/SectionNav/SectionNav";

/* ---------- helpers ---------- */
const Key = ({ children }) => <mark className="key">{children}</mark>;
const Term = ({ children }) => <strong className="term">{children}</strong>;

/* ---------- section shell ---------- */
const Section = ({ title, tr, children }) => {
  const isStringTitle = typeof title === "string";
  const match = isStringTitle ? title.match(/^\s*(\d+)\)\s*/) : null;
  const num = match ? `${match[1]}) ` : "";
  const cleanTitle = match ? title.replace(/^\s*\d+\)\s*/, "") : title;

  return (
    <section
      className="g-section"
      id={cleanTitle
        ?.toString()
        .toLowerCase()
        .replace(/[^\w]+/g, "-")}
    >
      <h2>
        {num}
        {tr} <span className="en-tag">({cleanTitle})</span>
      </h2>
      {children}
    </section>
  );
};

const Rule = ({ children }) => <div className="rule-box">{children}</div>;

const Examples = ({ items }) => (
  <div className="examples">
    {items.map((ex, i) => (
      <p key={i}>
        <strong>{ex.de}</strong>  {ex.tr}{" "}
        <span className="pron">({ex.pron})</span>
      </p>
    ))}
  </div>
);

/* ---------- callouts / formula / mini-table ---------- */
const Callout = ({ type = "tip", title, children }) => (
  <div className={`callout ${type}`}>
    {title && <div className="callout-title">{title}</div>}
    <div className="callout-body">{children}</div>
  </div>
);

const Formula = ({ rows = [] }) => (
  <div className="formula">
    {rows.map((r, i) => (
      <div className="formula-row" key={i}>
        <span className="part">{r[0]}</span>
        <span className="arrow"></span>
        <span className="result">{r[1]}</span>
        {r[2] && <span className="hint-inline">{r[2]}</span>}
      </div>
    ))}
  </div>
);

const MiniTable = ({ head = [], rows = [] }) => (
  <div className="mini-table-wrap">
    <table className="mini-table">
      {head.length > 0 && (
        <thead>
          <tr>
            {head.map((h, i) => (
              <th key={i}>{h}</th>
            ))}
          </tr>
        </thead>
      )}
      <tbody>
        {rows.map((r, i) => (
          <tr key={i}>
            {r.map((c, j) => (
              <td key={j}>{c}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  </div>
);

/* ---------- minimalist accordion (pb2) ---------- */
const Pb2Item = ({ id, title, bullet, open, onToggle, children }) => {
  const innerRef = useRef(null);
  const [maxH, setMaxH] = useState(0);

  useEffect(() => {
    const measure = () => {
      if (innerRef.current) setMaxH(innerRef.current.scrollHeight);
    };
    measure();
    const ro = new ResizeObserver(measure);
    if (innerRef.current) ro.observe(innerRef.current);
    window.addEventListener("resize", measure);
    return () => {
      ro.disconnect();
      window.removeEventListener("resize", measure);
    };
  }, [open, children]);

  return (
    <div className={`pb2-item ${open ? "is-open" : ""}`}>
      <button
        type="button"
        className="pb2-head"
        onClick={() => onToggle(id)}
        aria-expanded={open}
      >
        <span className="pb2-title">{title}</span>
        <span className="pb2-bullet">{bullet}</span>
        <span className="pb2-caret" aria-hidden>
          
        </span>
      </button>
      <div
        className="pb2-body"
        style={{ maxHeight: open ? `${maxH}px` : 0 }}
        aria-hidden={!open}
      >
        <div ref={innerRef} className="pb2-inner">
          {children}
        </div>
      </div>
    </div>
  );
};

/* ---------- 1) Aussprache (A2) ---------- */
const PronunciationA2DE = () => {
  const ids = ["auslaut", "en", "ig", "bindung", "wortakzent"];
  const [openMap, setOpenMap] = useState(
    Object.fromEntries(ids.map((id) => [id, false]))
  );
  const toggle = (id) => setOpenMap((m) => ({ ...m, [id]: !m[id] }));

  return (
    <Section title="1) Pronunciation A2" tr="Telaffuz (A2 odakl)">
      <p className="intro">
        A2de kula rahatlatan noktalar: <Term>Auslautverhrtung</Term> (kelime
        sonunda sertleme), zayf <Term>-en</Term>, <Term>-ig</Term> ve cmle
        ak (balama).
      </p>

      <div className="pb2-list">
        <Pb2Item
          id="auslaut"
          open={openMap["auslaut"]}
          onToggle={toggle}
          title="Auslautverhrtung: b/d/g  p/t/k"
          bullet="Tag  tak; Hund  hunt"
        >
          <div className="howto">
            Kelime sonunda ses yumuamaz: <code>Tag</code>  <code>tak</code>,{" "}
            <code>Abend</code>  <code>abnt</code>. Cmle iinde yumuar:{" "}
            <code>Tage</code>  <code>tage</code>.
          </div>
        </Pb2Item>

        <Pb2Item
          id="en"
          open={openMap["en"]}
          onToggle={toggle}
          title="-en zayf: n"
          bullet="machen  mahn; laufen  laufn"
        >
          <ul className="tight">
            <li>
              <code>-en</code> sonu ou zaman ksa <u></u> gibi:{" "}
              <code>machen</code> {" "}
              <code>
                mah<span>n</span>
              </code>
            </li>
            <li>
              ok hzl konumada <code>-n</code> kalr: <code>gehen</code> {" "}
              <code>
                gee<span>n</span>
              </code>
            </li>
          </ul>
        </Pb2Item>

        <Pb2Item
          id="ig"
          open={openMap["ig"]}
          onToggle={toggle}
          title="ig (sonda)  ih"
          bullet="knig  knih; ruhig  ruhih"
        >
          <div className="howto">
            Son hecede <code>-ig</code> genelde <em>ich</em> sesine yaklar.
          </div>
        </Pb2Item>

        <Pb2Item
          id="bindung"
          open={openMap["bindung"]}
          onToggle={toggle}
          title="Balama (Liaison)"
          bullet="ich_arbeite  i-char-bay-te"
        >
          <div className="howto">
            Sonundaki sessiz, sonraki sesliyle birleir:{" "}
            <code>ich arbeite</code>  <code>i-charbayte</code>. Ak doal
            olur.
          </div>
        </Pb2Item>

        <Pb2Item
          id="wortakzent"
          open={openMap["wortakzent"]}
          onToggle={toggle}
          title="Wortakzent"
          bullet="MEI-stens erste Silbe: AR-beit, LEH-rer"
        >
          <div className="howto">
            Yerli Almanca kelimelerde vurgu genelde ilk hece; alnt kelimelerde
            deiebilir: Ho-te<span>L</span>.
          </div>
        </Pb2Item>
      </div>
    </Section>
  );
};

/* ---------- practice helpers ---------- */
function normalize(s = "") {
  return s.toLowerCase().replaceAll("", "'").replace(/\s+/g, " ").trim();
}

/* ---------- Quick Practice (DE A2) ---------- */
const PRACTICE_ITEMS_DE_A2 = [
  {
    id: "perfekt-haben",
    promptBefore: "Perfekt: Ich ",
    promptAfter: " die Hausaufgaben gemacht.",
    answers: ["habe"],
    distractors: ["habe", "bin", "hat"],
  },
  {
    id: "perfekt-sein",
    promptBefore: "Perfekt: Wir ",
    promptAfter: " ins Kino gegangen.",
    answers: ["sind"],
    distractors: ["sind", "waren", "haben"],
  },
  {
    id: "trennbar",
    promptBefore: "Trennbar: Ich ",
    promptAfter: " dich morgen ",
    answers: [
      "rufe an",
      "rufe  an",
      "rufe  dich  morgen  an".replace(/\s+/g, " "),
    ],
    distractors: ["rufe an", "anrufe", "rufe"],
  },
  {
    id: "weil-ende",
    promptBefore: "Nebensatz: Ich bleibe zu Hause, weil ich ",
    promptAfter: ".",
    answers: ["krank bin"],
    distractors: ["bin krank", "krank bin", "krank"],
  },
  {
    id: "wohin-akk",
    promptBefore: "Wohin? Ich stelle die Tasse ",
    promptAfter: " Tisch.",
    answers: ["auf den"],
    distractors: ["auf dem", "auf den", "auf die"],
  },
  {
    id: "wo-dat",
    promptBefore: "Wo? Die Tasse steht ",
    promptAfter: " Tisch.",
    answers: ["auf dem"],
    distractors: ["auf dem", "auf den"],
  },
  {
    id: "relativ-akk",
    promptBefore: "Relativ: Das ist der Mann, ",
    promptAfter: " ich kenne.",
    answers: ["den"],
    distractors: ["der", "den", "dem"],
  },
  {
    id: "komp",
    promptBefore: "Komparativ: billig  ",
    promptAfter: " als",
    answers: ["billiger"],
    distractors: ["mehr billig", "billiger", "am billigsten"],
  },
  {
    id: "modal-prat",
    promptBefore: "Prteritum: Frher ",
    promptAfter: " ich nicht schwimmen. (knnen)",
    answers: ["konnte"],
    distractors: ["konnte", "kann", "knnte"],
  },
  {
    id: "seit",
    promptBefore: "Zeit: Ich wohne hier ",
    promptAfter: " 2019.",
    answers: ["seit"],
    distractors: ["seit", "vor", "ab"],
  },
];

function QuickPracticeDE_A2() {
  const [values, setValues] = useState(() =>
    Object.fromEntries(PRACTICE_ITEMS_DE_A2.map((i) => [i.id, ""]))
  );
  const [submitted, setSubmitted] = useState(false);

  const result = useMemo(() => {
    const r = {};
    PRACTICE_ITEMS_DE_A2.forEach((it) => {
      const user = normalize(values[it.id]);
      const ok = it.answers.some((a) => normalize(a) === user);
      r[it.id] = { ok, user, answers: it.answers, distractors: it.distractors };
    });
    return r;
  }, [values]);

  const correctCount = useMemo(
    () => Object.values(result).filter((x) => x.ok).length,
    [result]
  );

  const handleChange = (id, v) => setValues((s) => ({ ...s, [id]: v }));
  const handleSubmit = () => setSubmitted(true);
  const handleReset = () => {
    setValues(Object.fromEntries(PRACTICE_ITEMS_DE_A2.map((i) => [i.id, ""])));
    setSubmitted(false);
  };
  const hint = (opts = []) => (opts.length ? `(${opts.join(" / ")})` : "");

  return (
    <div className="qp">
      <div className="qp-list">
        {PRACTICE_ITEMS_DE_A2.map((it, idx) => {
          const state = result[it.id];
          const cls =
            submitted && state
              ? state.ok
                ? "qp-input is-correct"
                : "qp-input is-wrong"
              : "qp-input";
          return (
            <div className="qp-item" key={it.id}>
              <div className="qp-prompt">
                <span className="idx">{idx + 1}.</span>{" "}
                <span>{it.promptBefore}</span>
                <input
                  aria-label={`Soru ${idx + 1} iin cevap`}
                  className={cls}
                  type="text"
                  value={values[it.id]}
                  onChange={(e) => handleChange(it.id, e.target.value)}
                  disabled={submitted}
                  onKeyDown={(e) => {
                    if (e.key === "Enter" && !submitted) handleSubmit();
                  }}
                />
                <span>{it.promptAfter}</span>{" "}
                <span className="hint">{hint(it.distractors)}</span>
              </div>

              {submitted && !state.ok && (
                <div className="qp-feedback">
                   Doru cevap: <em>{it.answers[0]}</em>
                </div>
              )}
              {submitted && state.ok && (
                <div className="qp-feedback ok"> Doru</div>
              )}
            </div>
          );
        })}
      </div>

      <div className="qp-actions">
        {!submitted ? (
          <button className="primary-btn" onClick={handleSubmit}>
            Kontrol et
          </button>
        ) : (
          <>
            <div className="qp-score">
              {correctCount} / {PRACTICE_ITEMS_DE_A2.length} doru
            </div>
            <button className="ghost-btn" onClick={handleReset}>
              Yeniden balat
            </button>
          </>
        )}
      </div>
    </div>
  );
}

/* ---------- Mini-MC (DE A2) ---------- */
const MC_ITEMS_DE_A2 = [
  {
    q: "Wechselprpositionen: Hangi kural doru?",
    choices: [
      "Wo?  Akkusativ",
      "Wohin?  Dativ",
      "Wo?  Dativ, Wohin?  Akkusativ",
    ],
    correctIndex: 2,
    tr: "Yer (Wo?) = Dativ  Yn (Wohin?) = Akkusativ.",
  },
  {
    q: "Nebensatzta fiilin yeri?",
    choices: ["Sonda", "2. pozisyon", "Baa"],
    correctIndex: 0,
    tr: "weil/dass/wenn   fiil EN SONDA.",
  },
  {
    q: "Hangi fiiller Perfekt'te ounlukla 'sein' alr?",
    choices: [
      "Hareket/deiim fiilleri",
      "Sahiplik fiilleri",
      "Dnce fiilleri",
    ],
    correctIndex: 0,
    tr: "gehen, kommen, fahren, bleiben (istisna), sterben",
  },
];

function MiniMC_DE_A2() {
  const [answers, setAnswers] = useState({});
  return (
    <div className="mini-mc">
      {MC_ITEMS_DE_A2.map((it, i) => {
        const picked = answers[i];
        const isDone = picked !== undefined;
        return (
          <div className="mini-mc-item" key={i}>
            <div className="q">
              {i + 1}. {it.q}
            </div>
            <div className="choices">
              {it.choices.map((c, idx) => {
                const isCorrect = idx === it.correctIndex;
                const cls = isDone
                  ? idx === picked
                    ? isCorrect
                      ? "choice correct"
                      : "choice wrong"
                    : "choice disabled"
                  : "choice";
                return (
                  <button
                    key={idx}
                    className={cls}
                    onClick={() => setAnswers((s) => ({ ...s, [i]: idx }))}
                    disabled={isDone}
                    aria-label={`Seenek ${idx + 1}`}
                  >
                    {c}
                  </button>
                );
              })}
            </div>
            {isDone && (
              <div
                className={`mc-feedback ${
                  answers[i] === it.correctIndex ? "ok" : "bad"
                }`}
              >
                {answers[i] === it.correctIndex ? " Doru!" : " Yanl."}{" "}
                <span className="tr">{it.tr}</span>
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}

