import React from "react";
import {
  Key,
  Examples,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function PrepositionsTime() {
  return (
    <>
      <div className="rule-box">
        <strong>Zaman edatlar</strong>: <Key>in / on / at</Key> + tipik
        elemeler, ayrca <Key>ago / in (future) / until / by / fromto</Key>.
      </div>

      <Sub title="in / on / at (time uses)" />
      <MiniTable
        head={["Edat", "Kullanm", "rnek"]}
        rows={[
          [
            "in",
            "aylar, yllar, mevsimler, uzun aralklar",
            "in June, in 2020, in winter, in the morning",
          ],
          ["on", "gnler, tarihler", "on Monday, on 5th May"],
          [
            "at",
            "saat, ksa noktalar",
            "at 7 oclock, at night, at the weekend (UK)",
          ],
        ]}
      />

      <Sub title="Dierleri (Others)" />
      <MiniTable
        head={["fade", "Anlam", "rnek"]}
        rows={[
          ["ago", "gemite", "two days ago"],
          ["in (future)", "gelecekten itibaren", "in two days (iki gn sonra)"],
          ["until / till", "e kadar", "I will wait until 6."],
          ["by", "e kadar (en ge)", "Finish this by Friday."],
          ["from  to ", "den e kadar", "from 9 to 5"],
        ]}
      />

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "We have a meeting on Friday at 10.",
            tr: "Cuma gn saat 10da toplantmz var.",
            pron: "vii hev  miiting on fraydey et ten",
          },
          {
            en: "I started this job in 2021.",
            tr: "Bu ie 2021de baladm.",
            pron: "ay startd ds cab in tenti tenti van",
          },
          {
            en: "Please finish it by tomorrow.",
            tr: "Ltfen bunu yarna kadar bitir.",
            pron: "pliiz fini it bay tmorou",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="in / on / at / ago / by"
        items={[
          {
            id: "pt1",
            before: "The class starts ___ 9 a.m.",
            after: "",
            answers: ["at"],
          },
          {
            id: "pt2",
            before: "My birthday is ___ March.",
            after: "",
            answers: ["in"],
          },
          {
            id: "pt3",
            before: "We met ___ Monday.",
            after: "",
            answers: ["on"],
          },
          {
            id: "pt4",
            before: "I called her two days ___.",
            after: "",
            answers: ["ago"],
          },
          {
            id: "pt5",
            before: "Finish the report ___ Friday.",
            after: "",
            answers: ["by"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Doru olan?",
            choices: ["in Monday", "on Monday", "at Monday"],
            correctIndex: 1,
            explain: "Gnlerde on.",
          },
        ]}
      />
    </>
  );
}
