import React from "react";
import {
  Key,
  Term,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function Articles() {
  return (
    <>
      <div className="rule-box">
        <Term>a/an</Term> (belirtisiz)  <Term>the</Term> (belirli) {" "}
        <Term></Term> (sfr artikel, genel).
      </div>

      <Sub title="a / an (ses nemli) (sound matters)" />
      <MiniTable
        head={["Kural", "rnek", "Not"]}
        rows={[
          [
            "a + nsz sesi",
            "a book, a dog, a university",
            "university /ju/  nsz sesi: a",
          ],
          [
            "an + nl sesi",
            "an apple, an hour, an umbrella",
            "hour /a-/  an",
          ],
        ]}
      />
      <Examples
        items={[
          { en: "I have a car.", tr: "Arabam var.", pron: "ay hev  kar" },
          {
            en: "She eats an apple.",
            tr: "O bir elma yer.",
            pron: "ii iits en epl",
          },
        ]}
      />

      <Sub title="the (belirli) (definite)" />
      <Callout type="tip" title="Ne zaman the?">
        <ul className="compact-list">
          <li>
            Tek, bilinen eyler: <em>the sun, the moon</em>
          </li>
          <li>
            Bahsedilmi/zgl: <em>the book on the table</em>
          </li>
          <li>
            Superlatives: <em>the best, the biggest</em>
          </li>
        </ul>
      </Callout>
      <Examples
        items={[
          {
            en: "Open the door, please.",
            tr: "Kapy a ltfen.",
            pron: "oupn d dor pliiz",
          },
          {
            en: "The sun is bright.",
            tr: "Gne parlak.",
            pron: "d san iz brayt",
          },
        ]}
      />

      <Sub title="Artikelsiz kullanm () (Zero article)" />
      <Callout type="tip" title="Genel konuurken">
        <ul className="compact-list">
          <li>
            Genel oul/saylamayan:{" "}
            <em>Milk is healthy. Books are expensive.</em>
          </li>
          <li>
            Yemek isimleri: <em>We have breakfast at 8.</em>
          </li>
          <li>
            Diller/ehirler/lkeler:{" "}
            <em>She speaks English. I live in Ankara.</em>
          </li>
        </ul>
      </Callout>

      <Sub title="Mini Szlk (Mini Vocabulary)" />
      <MiniTable
        head={["fade", "eviri", "Not"]}
        rows={[
          [
            "have breakfast/lunch/dinner",
            "kahvalt/le/akam yemei yemek",
            "",
          ],
          ["go to school/work", "okula/ie gitmek", "ou kullanmda "],
          ["play the guitar", "gitar almak", "belirli enstrmanlarda the"],
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="a / an / the / "
        items={[
          {
            id: "ar1",
            before: "I have ___ umbrella.",
            after: "",
            answers: ["an"],
          },
          {
            id: "ar2",
            before: "He is ___ engineer.",
            after: "",
            answers: ["an"],
          },
          {
            id: "ar3",
            before: "Open ___ window, please.",
            after: "",
            answers: ["the"],
          },
          {
            id: "ar4",
            before: "___ sugar is sweet.",
            after: "",
            answers: ["", ""],
            hint: "genel ifade",
          },
          {
            id: "ar5",
            before: "She bought ___ book.",
            after: "",
            answers: ["a"],
          },
        ]}
      />
      <ExerciseMC
        items={[
          {
            q: "Hangi cmlede *an* gerekir?",
            choices: ["___ university", "___ orange", "___ house"],
            correctIndex: 1,
            explain: "orange nl sesi ile balar: an orange.",
          },
          {
            q: "Hangisi genel ifade ()?",
            choices: [
              "The water is cold.",
              "Water is important.",
              "A water is cold.",
            ],
            correctIndex: 1,
            explain: "Genel: Water is important.",
          },
        ]}
      />
    </>
  );
}
