import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function WhQuestions() {
  return (
    <>
      <div className="rule-box">
        <Term>Wh-words:</Term> <Key>What / Where / When / Who / Why / How</Key>{" "}
        (+ <Key>How old/many/much/long/often</Key>). Genel kalp:{" "}
        <Key>Wh + yardmc (do/does/is/are) + zne + Fiil</Key>
      </div>

      <Sub title="Yap (Structure)" />
      <MiniTable
        head={["Tr", "ablon", "rnek"]}
        rows={[
          ["be", "Wh + am/is/are + zne  ?", "Where are you?"],
          [
            "genel fiil",
            "Wh + do/does + zne + V1  ?",
            "What do you do? / Where does he live?",
          ],
          ["zne sorusu", "Who + V1  (do/does yok)", "Who lives here?"],
          [
            "miktar",
            "How many/much + isim + ...?",
            "How many books do you have?",
          ],
          ["ya", "How old + be + zne?", "How old are you?"],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog (Examples & Mini-Dialogue)" />
      <Examples
        items={[
          {
            en: "Where are you from?",
            tr: "Nerelisin?",
            pron: "ver ar yu from",
          },
          {
            en: "What do you do?",
            tr: "Ne i yapyorsun?",
            pron: "vat du yu du",
          },
          {
            en: "Who lives here?",
            tr: "Burada kim yayor?",
            pron: "hu livz hir",
          },
          {
            en: "How old is she?",
            tr: "O ka yanda?",
            pron: "hav old iz ii",
          },
        ]}
      />
      <Callout type="tip" title="Mini-Diyalog (Mini-Dialogue)">
        <p>
          <strong>A:</strong> Where do you work?
          <br />
          <strong>B:</strong> I work in Ankara. What do you do?
        </p>
      </Callout>

      <Callout
        type="tip"
        title="zne vs Nesne Sorular (Subject vs Object Questions)"
      >
        zne arandnda <Key>do/does</Key> yok: <em>Who lives here?</em>
        Nesne arandnda <Key>do/does</Key> var:{" "}
        <em>Who does he live with?</em>
      </Callout>

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Doru Wh-word ile doldur (Fill with the correct Wh-word)"
        items={[
          {
            id: "wh1",
            before: "_____ are you? (naslsn?)",
            after: "",
            answers: ["How", "how"],
          },
          {
            id: "wh2",
            before: "_____ does he live?",
            after: "",
            answers: ["Where", "where"],
          },
          {
            id: "wh3",
            before: "_____ is your birthday?",
            after: "",
            answers: ["When", "when"],
          },
          {
            id: "wh4",
            before: "_____ are they here? (neden)",
            after: "",
            answers: ["Why", "why"],
          },
          {
            id: "wh5",
            before: "_____ lives in this house?",
            after: "",
            answers: ["Who", "who"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangi soruda *do/does* GEREKMEZ?",
            choices: [
              "Where do you live?",
              "Who lives here?",
              "Why does she work?",
            ],
            correctIndex: 1,
            explain: "zne sorusu: Who lives (do/does yok).",
          },
          {
            q: "Doru kalp hangisi?",
            choices: [
              "How many money do you have?",
              "How much books do you have?",
              "How many books do you have?",
            ],
            correctIndex: 2,
            explain: "countablemany, uncountablemuch.",
          },
        ]}
      />
    </>
  );
}
