import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function PresentPerfectContinuous() {
  return (
    <>
      <div className="rule-box">
        <strong>Present Perfect Continuous</strong> ={" "}
        <Key>have/has been + fiil-ing</Key>. <Term>Sre + imdi etkisi</Term>:{" "}
        <em>for/since, lately/recently</em>.
      </div>

      <Sub title="Kurulu (Formation)" />
      <Formula
        rows={[
          [
            "Olumlu",
            "zne + have/has been + fiil-ing",
            "I have been studying since 5.",
          ],
          [
            "Olumsuz",
            "zne + have not/has not been + fiil-ing",
            "She has not been sleeping well.",
          ],
          [
            "Soru",
            "Have/Has + zne + been + fiil-ing?",
            "Have you been working out?",
          ],
        ]}
      />

      <Sub title="Present Perfect mi, Present Perfect Continuous m? (Which to use?)" />
      <MiniTable
        head={["Yap", "Odak", "rnek"]}
        rows={[
          ["Present Perfect", "sonu", "I have finished my homework."],
          [
            "Present Perfect Continuous",
            "sre/aktivite",
            "I have been doing my homework for 2 hours.",
          ],
        ]}
      />

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "He has been learning English for a year.",
            tr: "Bir yldr ngilizce reniyor.",
            pron: "hi hez bin lrn'ing ingli for  yir",
          },
          {
            en: "We havent been sleeping well lately.",
            tr: "Son zamanlarda iyi uyuyamyoruz.",
            pron: "vii hevnt bin sliiping vel leytli",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="have/has been + fiil-ing"
        items={[
          {
            id: "ppc1",
            before: "She _____ (learn) German since last year.",
            after: "",
            answers: ["has been learning"],
          },
          {
            id: "ppc2",
            before: "I _____ (not / feel) well lately.",
            after: "",
            answers: ["have not been feeling", "havent been feeling"],
          },
          {
            id: "ppc3",
            before: "_____ you _____ (work) here long?",
            after: "",
            answers: ["have you been working"],
          },
          {
            id: "ppc4",
            before: "They _____ (study) for two hours.",
            after: "",
            answers: ["have been studying"],
          },
          {
            id: "ppc5",
            before: "He _____ (not / sleep) enough.",
            after: "",
            answers: ["has not been sleeping", "hasnt been sleeping"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangi cmlede Present Perfect Continuous daha uygun?",
            choices: [
              "I have written the report.",
              "I have been writing the report for 2 hours.",
              "I wrote the report yesterday.",
            ],
            correctIndex: 1,
            explain: "Sre vurgusu  have/has been + fiil-ing.",
          },
        ]}
      />
    </>
  );
}
