import React from "react";
import {
  Key,
  Term,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function ReportedSpeech() {
  return (
    <>
      <div className="rule-box">
        <strong>Aktarma Cmlesi (Reported Speech)</strong>: Bakasnn szn
        aktarma. Temel zaman kaymalar:
        <MiniTable
          head={["Dorudan (Direct)", "Aktarma (Reported)", "rnek"]}
          rows={[
            ["am/is/are", "was/were", "I am tired.  He said he was tired."],
            ["do/does", "did", "She likes tea.  He said she liked tea."],
            ["will", "would", "I will call.  She said she would call."],
            ["can", "could", "I can swim.  He said he could swim."],
          ]}
        />
      </div>

      <Sub title="Zamir / Zaman / Yer deiimleri (Pronoun / Tense / Place shifts)" />
      <Callout type="tip" title="Kk dnmler (Common shifts)">
        <ul className="compact-list">
          <li>
            I  he/she  this  that  now  then  today  that day  here {" "}
            there
          </li>
          <li>
            Emir: <em>Dont be late.</em>  He told me{" "}
            <Term>not to be late</Term>.
          </li>
        </ul>
      </Callout>

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "I am busy.  She said she was busy.",
            tr: "Megulm.  Megul olduunu syledi.",
            pron: "i sed i voz bizi",
          },
          {
            en: "We will go.  They said they would go.",
            tr: "Gideceiz.  Gideceklerini sylediler.",
            pron: "dey sed dey vud gou",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Direct  Reported"
        items={[
          {
            id: "rs1",
            before: "I am tired.  He said he ____ tired.",
            after: "",
            answers: ["was"],
          },
          {
            id: "rs2",
            before: "She will come.  He said she ____ come.",
            after: "",
            answers: ["would"],
          },
          {
            id: "rs3",
            before: "They are here.  He said they ____ there.",
            after: "",
            answers: ["were"],
          },
          {
            id: "rs4",
            before: "Dont be late.  He told me ____ be late.",
            after: "",
            answers: ["not to"],
          },
          {
            id: "rs5",
            before: "I can help.  She said she ____ help.",
            after: "",
            answers: ["could"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Doru dnm hangisi?",
            choices: [
              "I will come.  She said she will come.",
              "I will come.  She said she would come.",
              "I will come.  She said she comes.",
            ],
            correctIndex: 1,
            explain: "will  would (zaman kaymas).",
          },
        ]}
      />
    </>
  );
}
