import React from "react";
import {
  Key,
  Examples,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function Quantifiers() {
  return (
    <>
      <div className="rule-box">
        <strong>Saylabilen vs saylamayan</strong> isimlerle miktar
        belirteleri:{" "}
        <Key>
          some/any, much/many, a lot of/lots of, a few/a little, too/enough
        </Key>
        .
      </div>

      <Sub title="Countable vs Uncountable (saylabilen / saylamayan)" />
      <MiniTable
        head={["Tr", "rnek", "Miktar kelimeleri"]}
        rows={[
          [
            "Countable (oul olur)",
            "book(s), car(s), idea(s)",
            "many, few, a few",
          ],
          [
            "Uncountable (oul olmaz)",
            "water, sugar, money, advice",
            "much, little, a little",
          ],
        ]}
      />

      <Sub title="some / any / a lot of (temel kullanm  basic use)" />
      <MiniTable
        head={["Kural", "rnek", "Not"]}
        rows={[
          [
            "some (olumlu, teklif/rica)",
            "I have some time. / Would you like some tea?",
            "count/uncount",
          ],
          [
            "any (olumsuz/soru)",
            "I dont have any money. / Do you have any books?",
            "count/uncount",
          ],
          ["a lot of / lots of", "There are a lot of cars.", "count/uncount"],
        ]}
      />

      <Sub title="much / many  a few / a little (karlatrma  contrast)" />
      <MiniTable
        head={["Kural", "rnek", "Not"]}
        rows={[
          ["many (count)", "many books", ""],
          ["much (uncount)", "much water", ""],
          ["a few (count)", "a few friends", "az ama yeterli"],
          ["few (count)", "few friends", "az ve yetersiz (negatif)"],
          ["a little (uncount)", "a little time", "az ama yeterli"],
          ["little (uncount)", "little time", "az ve yetersiz (negatif)"],
        ]}
      />

      <Sub title="too / enough (derece  degree)" />
      <MiniTable
        head={["Kural", "rnek", "Not"]}
        rows={[
          ["too + sfat/zarf", "too expensive", "gereinden fazla"],
          ["sfat/zarf + enough", "cheap enough", "yeterince"],
          ["enough + isim", "enough money", ""],
        ]}
      />

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "I have a few questions.",
            tr: "Birka sorum var (yeterli).",
            pron: "ay hev  fyuu kwesnz",
          },
          {
            en: "We dont have much time.",
            tr: "Fazla vaktimiz yok.",
            pron: "vii dont hev ma taym",
          },
          {
            en: "Its too late. We dont have enough time.",
            tr: "ok ge. Yeterince zamanmz yok.",
            pron: "its tuu leyt. vii dont hev inaf taym",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Doru miktar kelimesi (Choose the correct quantifier)"
        items={[
          {
            id: "q1",
            before: "There arent _____ chairs.",
            after: "",
            answers: ["many"],
          },
          {
            id: "q2",
            before: "I have _____ money. (yeterli deil)",
            after: "",
            answers: ["little"],
          },
          {
            id: "q3",
            before: "We need _____ water.",
            after: "",
            answers: ["some", "a lot of"],
          },
          {
            id: "q4",
            before: "He has _____ friends. (az ama yeterli)",
            after: "",
            answers: ["a few"],
          },
          {
            id: "q5",
            before: "This car is _____ expensive.",
            after: "",
            answers: ["too"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangisi uncountable ile kullanlr?",
            choices: ["many water", "much water", "a few water"],
            correctIndex: 1,
            explain: "water uncountable  much.",
          },
        ]}
      />
    </>
  );
}
