import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function PresentPerfectBasics() {
  return (
    <>
      <div className="rule-box">
        <strong>Present Perfect</strong>:{" "}
        <Key>have/has + fiilin 3. hali (past participle)</Key>. Deneyim /{" "}
        <em>imdiyle balantl</em> sonu. Tipik zarflar:
        <Term> ever, never, just, already, yet, for, since</Term>. Zaman
        <strong> genelde sylemeyiz</strong>.
      </div>

      <Sub title="Kurulu (Formation)" />
      <Formula
        rows={[
          [
            "Olumlu",
            "zne + have/has + fiilin 3. hali",
            "I have seen it. / She has finished.",
          ],
          [
            "Olumsuz",
            "zne + have not / has not + fiilin 3. hali",
            "He has not called yet.",
          ],
          [
            "Soru",
            "Have/Has + zne + fiilin 3. hali?",
            "Have you ever been to London?",
          ],
        ]}
      />

      <Sub title="Zaman Zarflar (Time adverbs)" />
      <MiniTable
        head={["Zarf", "Kullanm", "rnek"]}
        rows={[
          [
            "ever / never",
            "deneyim",
            "Have you ever tried sushi? / I have never been there.",
          ],
          [
            "just / already / yet",
            "hemen imdi / oktan / henz (olumsuz & soru)",
            "I have just finished. / I have already done it. / He has not called yet.",
          ],
          [
            "for / since",
            "sre / balang",
            "I have lived here for 5 years. / since 2019.",
          ],
        ]}
      />

      <Sub title="Past Simple vs Present Perfect (Karlatrma  contrast)" />
      <Callout type="tip" title="Fark (Difference)">
        <ul className="compact-list">
          <li>
            <Term>Past Simple</Term>: <Key>zaman belli</Key> (yesterday, in
            2019). rn: I visited Paris in 2019.
          </li>
          <li>
            <Term>Present Perfect</Term>: <Key>zaman sylenmez</Key>,
            deneyim/sonu. rn: I have visited Paris.
          </li>
        </ul>
      </Callout>

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "I have already finished my homework.",
            tr: "devimi oktan bitirdim.",
            pron: "ay hev olredi finit may homvrk",
          },
          {
            en: "She has not arrived yet.",
            tr: "Henz varmad.",
            pron: "ii hez not rrayvd yet",
          },
          {
            en: "We have lived here since 2020.",
            tr: "2020den beri burada yayoruz.",
            pron: "vii hev livd hir sins tenti tenti",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="have/has + fiilin 3. hali (past participle)"
        items={[
          {
            id: "pp1",
            before: "Have you _____ (ever / be) to London?",
            after: "",
            answers: ["ever been"],
          },
          {
            id: "pp2",
            before: "She _____ (not / finish) yet.",
            after: "",
            answers: ["has not finished", "hasn't finished"],
          },
          {
            id: "pp3",
            before: "I _____ (just / see) Ali.",
            after: "",
            answers: ["have just seen"],
          },
          {
            id: "pp4",
            before: "They _____ (live) here _____ 2018.",
            after: "",
            answers: ["have lived since"],
          },
          {
            id: "pp5",
            before: "He _____ (already / do) his homework.",
            after: "",
            answers: ["has already done"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangi cmle Present Perfect ile UYUMAZ?",
            choices: [
              "I have seen that film yesterday.",
              "I have just seen that film.",
              "Have you ever seen that film?",
            ],
            correctIndex: 0,
            explain: "yesterday  Past Simple ister.",
          },
        ]}
      />
    </>
  );
}
