// src/pages/Grammar/registry/sections/en/b1/AdverbsDegree.jsx
import React from "react";
import {
  Key,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function AdverbsDegree() {
  return (
    <>
      <div className="rule-box">
        <strong>Derece/Miktar Zarflar (Adverbs of Degree)</strong>:{" "}
        <Key>really, very, quite</Key>, <Key>too</Key>, <Key>enough</Key>,{" "}
        <Key>sothat / suchthat</Key>.
      </div>

      <Sub title="Yerleim & Kullanm (Placement & Usage)" />
      <MiniTable
        head={["ge", "Yerleim", "rnek"]}
        rows={[
          [
            "really / very / quite",
            "sfat veya zarfn nnde",
            "really/very/quite interesting",
          ],
          [
            "too + sfat/zarf",
            "gereinden fazla",
            "too expensive / too slowly",
          ],
          ["sfat/zarf + enough", "yeterince", "warm enough / quickly enough"],
          ["enough + isim", "isimden nce", "enough money / enough time"],
        ]}
      />

      <Sub title="sothat / suchthat (nedensonu / degree)" />
      <MiniTable
        head={["Yap", "ablon", "rnek"]}
        rows={[
          ["sothat", "so + sfat/zarf + that", "It was so late that we left."],
          [
            "suchthat",
            "such + (sfat) + isim + that",
            "It was such a good film that we watched it twice.",
          ],
        ]}
      />

      <Sub title="rnekler (Examples)" />
      <Examples
        items={[
          {
            en: "Its quite cold but not too cold.",
            tr: "Olduka souk ama ok deil.",
            pron: "its kuayt kould bat not tuu kould",
          },
          {
            en: "The test was so hard that many students failed.",
            tr: "Snav o kadar zordu ki birok renci kald.",
            pron: "d test vaz sou haard det meni styudnts feyld",
          },
          {
            en: "We dont have enough time.",
            tr: "Yeterince zamanmz yok.",
            pron: "vii dont hev inaf taym",
          },
        ]}
      />

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Degree / sothat / suchthat"
        items={[
          {
            id: "ad1",
            before: "This is _____ (really / very) interesting.",
            after: "",
            answers: ["really interesting", "very interesting"],
          },
          {
            id: "ad2",
            before: "Its _____ late that we cant go.",
            after: "",
            answers: ["so"],
          },
          {
            id: "ad3",
            before: "It was _____ a good idea that we tried it.",
            after: "",
            answers: ["such"],
          },
          {
            id: "ad4",
            before: "We dont have _____ time.",
            after: "",
            answers: ["enough"],
          },
          {
            id: "ad5",
            before: "This coat is _____ expensive.",
            after: "",
            answers: ["too"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Doru eleme?",
            choices: ["enough + adj", "adj + enough", "too + noun"],
            correctIndex: 1,
            explain:
              "sfat + enough (warm enough); enough + isim (enough money).",
          },
        ]}
      />
    </>
  );
}
