import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function ToBe() {
  return (
    <>
      <div className="rule-box">
        <Term>to be</Term> = <Key>am / is / are</Key>. <Key>Iam</Key>,{" "}
        <Key>you/we/theyare</Key>, <Key>he/she/itis</Key>.
      </div>

      <Sub title="Olumlu / Olumsuz / Soru (Affirmative / Negative / Question)" />
      <Formula
        rows={[
          ["Olumlu", "zne + am/is/are + ...", "I am / You are / He is ..."],
          ["Olumsuz", "am not / isnt / arent", "She isnt busy."],
          ["Soru", "Am/Is/Are + zne + ... ?", "Are they teachers?"],
        ]}
      />

      <Sub title="Ksaltmalar & Ksa Cevaplar (Contractions & Short Answers)" />
      <MiniTable
        head={["Ksaltma", "Tam", "Ksa cevap r."]}
        rows={[
          ["Im", "I am", ""],
          [
            "youre / were / theyre",
            "are",
            "Yes, we are. / No, they arent.",
          ],
          ["hes / shes / its", "is", "Yes, he is. / No, she isnt."],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog (Examples & Mini-Dialogue)" />
      <Examples
        items={[
          { en: "I am tired.", tr: "Yorgunum.", pron: "ay em tayrd" },
          { en: "He is at home.", tr: "O evde.", pron: "hii z et houm" },
          { en: "We arent late.", tr: "Ge deiliz.", pron: "vii aarnt leyt" },
        ]}
      />
      <Callout type="tip" title="Mini-Diyalog (Mini-Dialogue)">
        <p>
          <strong>A:</strong> Are you ready?
          <br />
          <strong>B:</strong> Yes, I am.
        </p>
      </Callout>

      <Callout type="tip" title="Ya ifadesi (Age Expression)">
        ngilizce yata <Term>be</Term> kullanlr: <em>I am 20.</em> (TR: 20
        yandaym)  <em>I have 20</em> 
      </Callout>

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Boluk Doldurma (Gap-fill): am / is / are"
        items={[
          { id: "be1", before: "I ___ a student.", after: "", answers: ["am"] },
          { id: "be2", before: "She ___ at work.", after: "", answers: ["is"] },
          {
            id: "be3",
            before: "We ___ not hungry.",
            after: "",
            answers: ["are", "aren't", "are not"],
          },
          {
            id: "be4",
            before: "___ you from Turkey?",
            after: "",
            answers: ["Are"],
          },
          {
            id: "be5",
            before: "He ___ 22 years old.",
            after: "",
            answers: ["is"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Doru dizi hangisi?",
            choices: [
              "I is / You are / He are",
              "I am / You are / He is",
              "I am / You is / He are",
            ],
            correctIndex: 1,
            explain: "I am  you/we/they are  he/she/it is.",
          },
          {
            q: "Ksa cevap: Are they here?",
            choices: ["Yes, they is.", "Yes, they are.", "Yes, they do."],
            correctIndex: 1,
            explain: "Be ile ksa cevap: Yes, they are.",
          },
        ]}
      />
    </>
  );
}
