import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function PastSimple() {
  return (
    <>
      <div className="rule-box">
        <strong>Past Simple</strong> = <Key>gemite tamamlanan eylemler</Key>.
        Zaman iaretleri:{" "}
        <Term>yesterday, last (week/month/year), ago, in 2019</Term>.
      </div>

      <Sub title="Kurulu (Formation)" />
      <Formula
        rows={[
          [
            "Olumlu",
            "zne + fiilin gemi hali (2. hali)",
            "I watched a film. / He went home.",
          ],
          [
            "Olumsuz",
            "zne + did not + fiilin temel hali",
            "She did not like it.",
          ],
          ["Soru", "Did + zne + fiilin temel hali?", "Did you see Ali?"],
          ["Ksa cevap", "Yes, I did. / No, I did not.", ""],
        ]}
      />

      <Sub title="Dzenli / Dzensiz Fiiller (Regular / Irregular)" />
      <MiniTable
        head={["Tr", "Kural / rnek", "Not"]}
        rows={[
          ["Dzenli", "work  worked  play  played", "-ed"],
          ["-e sonu", "live  lived", "yalnz -d"],
          ["sessiz + y", "study  studied", "y  ied"],
          ["Dzensiz", "gowent, havehad, seesaw, makemade", "ezber"],
        ]}
      />

      <Sub title="Sk Dzensizler  Mini Liste (Common irregulars  mini list)" />
      <MiniTable
        head={["Fiil (temel hali)", "Gemi hali", "Trke"]}
        rows={[
          ["go", "went", "gitmek"],
          ["see", "saw", "grmek"],
          ["have", "had", "sahip olmak / yemek"],
          ["do", "did", "yapmak"],
          ["come", "came", "gelmek"],
          ["make", "made", "yapmak, retmek"],
          ["take", "took", "almak"],
          ["get", "got", "almak, edinmek"],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog (Examples & Mini-Dialogue)" />
      <Examples
        items={[
          {
            en: "I visited my parents last weekend.",
            tr: "Geen hafta sonu ailemi ziyaret ettim.",
            pron: "ay vizitd may pernts last viikend",
          },
          {
            en: "She didnt watch TV yesterday.",
            tr: "Dn TV izlemedi.",
            pron: "ii didnt vo tii-vii yestrdey",
          },
          {
            en: "Did you go to the party?",
            tr: "Partiye gittin mi?",
            pron: "did yu gou t d parti",
          },
        ]}
      />
      <Callout type="tip" title="Mini-Diyalog (Mini-Dialogue)">
        <p>
          <strong>A:</strong> What did you do yesterday?
          <br />
          <strong>B:</strong> I studied English and cooked dinner.
        </p>
      </Callout>

      <Sub title="Altrma (Exercise)" />
      <ExerciseFill
        title="Gemi hali / did + fiilin temel hali (Past form / base after did)"
        items={[
          {
            id: "ps1",
            before: "She _____ (go) to school yesterday.",
            after: "",
            answers: ["went"],
          },
          {
            id: "ps2",
            before: "I _____ (not / see) him.",
            after: "",
            answers: ["didn't see", "did not see"],
          },
          {
            id: "ps3",
            before: "_____ you _____ (watch) the film?",
            after: "",
            answers: ["did you watch"],
          },
          {
            id: "ps4",
            before: "We _____ (have) dinner at 8.",
            after: "",
            answers: ["had"],
          },
          {
            id: "ps5",
            before: "He _____ (study) English last night.",
            after: "",
            answers: ["studied"],
          },
        ]}
      />
      <ExerciseMC
        title="Mini Test (Mini Quiz)"
        items={[
          {
            q: "Hangi zaman ifadesi genelde Past Simple?",
            choices: ["usually", "now", "yesterday"],
            correctIndex: 2,
            explain: "yesterday / last / ago  Past Simple.",
          },
          {
            q: "Olumsuz form doru olan?",
            choices: [
              "She didnt watched.",
              "She didnt watch.",
              "She not watch.",
            ],
            correctIndex: 1,
            explain: "did not + fiilin temel hali.",
          },
        ]}
      />
    </>
  );
}
